.App{
  
}
.header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 40px;
}
.my-logo{
  display: flex;
  flex-direction: row;

}
.my-icon{
  width: 44px;
  height: 44px;
  font-size: 28px;
  background-color: #de264b;
  color: white;
  border-radius: 50%;
  text-align: center;
  font-weight: 700;
  line-height: 42px;
}
.my-name{
  height: 34px;
  font-size: 22px;
  font-weight: 500;
  padding-left: 10px;
  padding-top: 5px;
  text-align: center;
}
.nav-bar{
  width: 250px;
  height: 44px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  line-height: 44px;
}
.nav-bar a{
  text-decoration: none;
  color: black;
  font-size: 14px;
}
.container{
  max-width: 960px;
  margin: 50px auto;
}
.about-me{
  margin: 5% 10%;
}
.my-photo img{
  width: 300px;
  height: 300px;
  border-radius: 1000px;
}
.my-photo{
  border: 3px solid #de264b;
  box-shadow: 0 0 25px 0 rgba(0,0,0,.1);
  border-radius: 1000px;
  margin-right: 20px;
  width: 300px;
  height: 300px;
}
.home-text{
  width:55%;
}
.about-me{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.my-major{
  font-size: 20px;
  color: #aaa;
  font-weight: 300;
}
.my-full-name{
  font-size: 50px;
  font-weight: 600;
}
.my-profile{
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  font-size: 20px;
}
.birthday i{
  margin-right: 5px;
}
.space{
  margin-left: 10px;
  margin-right: 10px;
}
.gender i{
  margin-right: 5px;
}
.introduce p{
  font-size: 14px;
  color: #666;
  font-weight: 400;
}
.download-btn{
  width: 150px;
  height: 46px;
  border: 2px solid #de264b;
  border-radius: 46px;
  line-height: 46px;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}
.download-btn:hover{
  background-color: #de264b;
  transition: all 0.4s  ease-in-out;
  color: white;
}
.resume{
  max-width: 960px;
  margin: 200px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.education{
  width: 50%;
}
.experience{
  width: 50%;
}
.title{
  font-size: 20px;
  font-weight: 600;
}
.title:before{
  width: 100%;
  background-color: #f5f5f5;
  height: 2px;
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  
}
.title{
  display: inline-block;
  position: relative;
  margin: 0 0 30px;
  padding-bottom: 7px;
}
.title:after{
  position: absolute;
  width: 30%;
  background-color: #de264b;
  content: "";
  height: 2px;
  display: block;
  bottom: 0;
}
.time-period{
  display: inline-block;
  height: 30px;
  line-height: 30px;
  width: 85px;
  border: 2px solid #de264b;
  border-radius: 30px;
  text-align: center;
  margin-right: 5px;
  margin-bottom: 10px;
}
.item-place{
  display: inline-block;
  color: #666;
  font-size: 14px;
}
.item-info{
  padding-left: 30px;
}
.skills>.title:after{
  width: 50%;
}
.skills{
  margin-top: 40px;
}
.contact{
  margin: 30px auto;
  max-width: 960px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.contact-item{
  width: 50%;
  padding-bottom: 30px;
  
}
.contact-icon{
  color: #de264b;
  display: inline-block;
  font-size: 40px;
  padding-right: 10px;
  position: relative;
  width: 10%;
  height: 100%;
}
.contact-icon i{
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.contact-info{
  display: inline-block;
  margin-left: 20px;
}
.contact-info h4{
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 18px;
}
.contact-info p{
  color: #aaa;
  font-size: 14px;
  font-weight: 500;
}
.footer{
  width: 100%;
  height: 60px;
  border-top: 2px solid #f2f2f2;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.footer-item{
  margin-top: 10px;
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  color: #a5a5a5;
  transition: 0.3s;
  text-align: center;
}
.fb:hover{
  color: white;
  background-color: #007bff;
  transition: 0.3s;

}
.google:hover{
  color: white;
  transition: 0.3s;
  background-color: #dc3545;
}
.github:hover{
  color: black;
  background-color: #666;
  transition: 0.3s;
}
#btn{
  position: fixed;
  cursor: pointer;
  bottom: 20px;
  right: 30px;
  font-size: 30px;
  width: 50px;
  height: 50px;
  background-color: white;
  box-shadow: 0 0 25px 0 rgba(0,0,0,.1);
  border-radius: 10px;
  text-align: center;
  line-height: 50px;
  transition: 0.3s;
}
#btn:hover{
  color: #de264b;
  transition: 0.3s;
}
@media(min-width : 480px) and (max-width : 900px){
  .education{
    width: 100%;
    margin-bottom: 20px;
    padding-left: 20px;
  }
  .experience{
    width: 100%;
    margin-bottom: 20px;
    padding-left: 20px;
  }
  .skills{
    width: 100%;
    margin-bottom: 20px;
    padding-left: 20px;
  }
  .resume{
    max-width: 600px;
  }
  .contact{
    max-width: 600px;

  }
  .contact-item{
    width: 100%;
    padding-left: 20px;
  }
  .my-photo{
    margin: 0 auto;
  }
  .home-text{
    margin: 20px auto;
    width: 100%;
    text-align: center;
  }
  .home-text p{
    text-align: left;
  }
}